import React, { useState } from 'react'
import { Link } from 'gatsby'
import cx from 'classnames'
import helpers from '../../../../helpers'

import styles from './Menu.module.scss'

import Exit from '../../../../images/svgs/exit.inline.svg'

const { LinksMenu } = helpers.constants.links


function Menu() {
    const [active, setActive] = useState(false)

    return (
        <div className={cx([styles.menu, { [styles.menu_active]: active }])}>
            <button
                className={styles.buttonMenu}
                onClick={() => setActive(!active)}
                type="button"
            >
                <span />
                <span />
                <span />
            </button>
            <nav className={styles.menu__wrapper}>
                <div className={styles.menu__container}>
                    {LinksMenu.map(item => (

                        <div key={item.title} className={cx([styles.menu__col, styles.col])}>

                            <div className={styles.col__title}>
                                <Link to={item.link}>
                                    <span className={styles.svg}>
                                        {item.icon}
                                    </span>
                                    {item.title}
                                </Link>
                            </div>

                            <div className={styles.col__items}>

                                {item.items.map(l => (
                                    <Link to={l.link} className={styles.col__item} key={l.text}>
                                        {l.text}
                                    </Link>
                                ))}

                            </div>

                        </div>

                    ))}
                    <div className={styles.exit}>
                        <Link to="/logout">
                            <Exit />
                            Выйти
                        </Link>
                    </div>
                </div>
                <button className={styles.chrest} onClick={() => setActive(false)} type="button">
                    <span />
                    <span />
                </button>
            </nav>
            <div className={styles.menu__overlay} onClick={() => setActive(false)} role="button" tabIndex="-1" />
        </div>
    )
}

export default Menu
